<template>
  <div>
    <img class="bg" src="@/assets/img/bg.jpg">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="login-form">
      <h1 class="h1"></h1>
                              <el-form-item v-if="tableName=='huiyuan'" label="" prop="huiyuannicheng">
        <el-input v-model="ruleForm.nicheng" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='huiyuan'" label="" prop="huiyuanmima">
        <el-input v-model="ruleForm.mima" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='huiyuan'" label="" prop="huiyuanxingming">
        <el-input v-model="ruleForm.xingming" placeholder=""></el-input>
      </el-form-item>
                                                            <el-form-item v-if="tableName=='huiyuan'" label="" prop="huiyuanshouji">
        <el-input v-model="ruleForm.shouji" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='huiyuan'" label="" prop="huiyuanshengri">
        <el-input v-model="ruleForm.shengri" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='huiyuan'" label="" prop="huiyuanshenfenzheng">
        <el-input v-model="ruleForm.shenfenzheng" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='huiyuan'" label="" prop="huiyuandizhi">
        <el-input v-model="ruleForm.dizhi" placeholder=""></el-input>
      </el-form-item>
                                                                                                                                                      <el-button @click="login()" class="btn-login" type="primary"></el-button>
    </el-form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      ruleForm: {
              },
      tableName:"",
      rules: {}
    };
  },
  mounted(){
    let table = this.$storage.get("loginTable");
    this.tableName = table;
  },
  methods: {
    // uuid
    getUUID () {
      return new Date().getTime();
    },
    // 
    login() {
                                                                                                                                                                                                                                                                                                if(`huiyuan` == this.tableName && this.ruleForm.shouji&&(!this.$validate.isMobile(this.ruleForm.shouji))){
        this.$message.error(``);
        return
      }
                                                                                                                        if(`huiyuan` == this.tableName && this.ruleForm.shenfenzheng&&(!this.$validate.checkIdCard(this.ruleForm.shenfenzheng))){
        this.$message.error(``);
        return
      }
                                                                                                                                                                                                            this.$refs["ruleForm"].validate(valid => {
        if (valid) {
          this.$http({
            url: `${this.tableName}/register`,
            method: "post",
            data:this.ruleForm
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.$router.replace({ path: "/login" });
            } else {
              this.$message.error(data.msg);
            }
          });
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
</style>
